@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap);
.nav{position: fixed;top: 0;padding:15px 20px;width: 100%;height: 50px;z-index: 1;transition: all 0.5s ease-in 0s;}
.nav__content{display: flex;justify-content: space-between;}
.nav__content .nav__logo{width: 120px;}
.avtar_netflix{width: 25px;height: 25px;cursor: pointer;    position: fixed;top: 10px;right: 10px}
.nav__black{background: #000;}
.banner{height: 500px;position: relative;object-fit: contain;color: #fff}
.banner__content{margin-left: 30px;padding-top: 140px;height: 200px;}
.banner__description{width: 500px;line-height: 1.3;padding-top: 20px;font-size: 16px;}
.banner__buttons button{cursor: pointer;color: #fff;outline: 0;border: 0;font-weight: 700;border-radius: 5px;padding: 10px 30px;background: #e50914;margin-right: 10px;margin-top: 10px;}
.banner__buttons button:hover{background: #fff;color: #000}
.banner__fade{height: 150px; background-image: linear-gradient(180deg, transparent, rgba(27, 27, 27, 0.61), #111);position: absolute;bottom: 0;left: 0;right: 0;}
.row{color: #fff;margin:10px 20px 30px;}
.row__posters{display: flex;overflow-y: hidden;overflow-x: scroll;padding: 20px;}
.row__posters::-webkit-scrollbar{display: none;}
.rowposter{max-height: 100px;object-fit: contain;margin-left: 10px;width: 100%;cursor: pointer;transition: -webkit-transform 450ms;transition: transform 450ms;transition: transform 450ms, -webkit-transform 450ms;}
.rowposter:hover{-webkit-transform: scale(1.08);transform: scale(1.08);}
.largeImage{max-height: 250px;}


.login{position: relative;height: 100vh;background:url('http://s3-us-west-2.amazonaws.com/techvibes/wp-content/uploads/2017/04/24135159/Netflix-Background.jpg') center no-repeat;background-size: cover;}
.login__logo{width: 120px;position: fixed;top: 10px;left: 10px;}
.login__button{background: #fc0a0a;padding: 10px 20px;color: #fff;border: 0;text-transform: uppercase;border-radius: 5px;position: fixed;top: 10px;right: 10px;}
.login__body{color: #fff;text-align: center;position: absolute;top: 50%;-webkit-transform: translateY(-50%);transform: translateY(-50%);left: 0;right: 0}
.login__body h1{font-size: 2.5rem;font-weight: bold;margin-bottom: 20px;}
.login__body h2{font-size: 1.625rem;font-weight: 400;margin-bottom: 10px;}
.login__body h3{font-size: 1.2rem;margin-bottom: 30px;}

.login__body .login__form{display: flex;align-items: center;justify-content: center;}
.login__body .login__form input{width: 300px;height: 40px;padding: 0 15px;}
.login__body .login__form button{height: 40px;border: 0;padding: 0 10px;background: #fc0a0a;font-size: 14px;text-transform: uppercase;cursor: pointer;color: #fff}
.login__gradient{width: 100%;z-index: 1;height: 100vh;background: rgba(0,0,0,0.4);background-image: linear-gradient(to top, rgba(0,0,0,0.8) 0, rgba(0,0,0,0) 60%, rgba(0,0,0,0) 100%);}
.signUpScreen{max-width: 350px;padding: 50px;background: rgba(0,0,0,0.85);margin: 0 auto;}
.signUpScreen form{display: grid;flex-direction: column;}
.signUpScreen form input{outline: 0;height: 40px;border: 0;margin-bottom: 15px;border-radius: 5px;padding: 5px 15px;} 
.signUpScreen form button{padding: 15px 20px;font-size: 16px;color: #fff;background: #e50914;border: 0;border-radius: 5px;cursor: pointer;margin-bottom: 20px;}
.signUpScreen form h4{color: grey;font-size: 14px;}
.signUpScreen form h4 span{color: #fff;}
.signUpScreen form h4:hover span{text-decoration: underline;}
body{font-family: 'Oxygen', sans-serif;background: #000;}
*{margin: 0;padding: 0;box-sizing: border-box;}

